import React from 'react'
import range from 'lodash/range'

import Box from './Box'
import Text from './Text'
import Flex from './Flex'
import Input from './Input'
import Circle from './Circle'

const Options = ({ disabled, circle, notsection, ...props }) => {
  return (
    <Box textAlign="center" {...props}>
      <Flex mt="0.5em">
        {!notsection && (
          <Box textAlign="center">
            <Text>完全</Text>
            <Text>不符合</Text>
          </Box>
        )}
        {range(5).map((num) => (
          <Flex px="0.4375em" flexDirection="column" alignItems="center" width={circle && 1 / 5} key={num}>
            <Box>{num + 1}</Box>
            { circle ? (
              <Box width={1}>
                <Circle border="1.675px solid" bg="lightGray" />
              </Box>
            ) : (
              <Input
                type="radio"
                name="points"
                alignItems="center"
                id={num + 1}
                value={num + 1}
                width={1}
              />
            )}
          </Flex>
        ))}
         {!notsection && (
          <Box textAlign="center">
            <Text>完全</Text>
            <Text>符合</Text>
          </Box>
        )}
      </Flex>
    </Box>
  )
}

export default Options
